<template>
  <div class="software-main">
    <div class="software-title">{{pageText[0]}}</div>
    <div class="software-text">{{pageText[1]}}</div>
    <div class="software-box">
      <div v-for="(item,index) in pageText[2].img" :key="index">
        <div class="software-box-item">
          <div class="software-box-img" :style="'background:url('+item.img+')no-repeat center;background-size:100% 100%'"></div>
          <div class="software-box-title">{{item.title}}</div>
          <div class="software-box-text">{{item.text}}</div>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      pageText:window.language.software
    }
  },

}
</script>

<style scoped>
.software-main
{
  width: 85rem;
  margin:6.25rem auto;
  text-align: center;
}
.software-title
{
  font-family: PingFang SC-Semibold, PingFang SC;
  font-size:3rem;
  font-weight: 600;
  line-height:4.1875rem;
}
.software-text
{
  font-family: PingFang SC-Semibold, PingFang SC;
  font-size:.875rem;
  font-weight: 600;
  line-height:1.25rem;
}
.software-box
{
  width: 40rem;
  margin: 6.25rem auto;
}
.software-box-item
{
  margin: 0 auto;
  padding-top: 3.25rem;
  width: 18.75rem;
  height: 23.1875rem;
  box-shadow: 0px 4px 15px rgba(0,0,0,0.1);
  border-radius: .625rem;
}
.software-box-text
{
  font-family: PingFang SC-Semibold, PingFang SC;
  font-size:1rem;
  font-weight: 600;
  line-height:1.375rem;
}
.software-box-title
{
  font-family: PingFang SC-Semibold, PingFang SC;
  font-size:1.75rem;
  font-weight: 600;
  line-height:2.5rem;
  margin-bottom: 1.25rem;
  padding-top: 3.125rem;
}
.software-box-img
{

  margin: 0 auto;
  width: 6.25rem;
  height: 6.25rem;
}
</style>



