import Vue from 'vue'
import Router from 'vue-router'
// 首页
import home from '@/page/home'

import adjust from '@/page/qita/adjust'
import interest from '@/page/qita/interest'

import mt5 from '@/page/mt5/mt5'
import fix from '@/page/mt5/FIX API'

import lianxi from '@/page/guanyu/lianxi'
import falv from '@/page/guanyu/falv'
import jieshao from '@/page/guanyu/jieshao'
import companion from '@/page/guanyu/companion'
import lever from '@/page/guanyu/lever'
import zhaopin from '@/page/guanyu/zhaopin'



import zhengquan from '@/page/shichang/zhengquan'
import gupiao from '@/page/shichang/gupiao'
import huobi from '@/page/shichang/huobi'
import dazong from '@/page/shichang/dazong'
import jinshu from '@/page/shichang/jinshu'
import qihuo from '@/page/shichang/qihuo'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/adjust',
      name: 'adjust',
      component: adjust
    },
    {
      path: '/interest',
      name: 'interest',
      component: interest
    },
    {
      path: '/mt5',
      name: 'mt5',
      component: mt5,
    },
    {
      path: '/fix',
      name: 'fix',
      component: fix,
    },
    {
      path: '/lianxi',
      name: 'lianxi',
      component: lianxi
    },
    {
      path: '/falv',
      name: 'falv',
      component: falv
    },
    {
      path: '/jieshao',
      name: 'jieshao',
      component: jieshao
    },
    {
      path: '/companion',
      name: 'companion',
      component: companion
    },
    {
      path: '/lever',
      name: 'lever',
      component: lever
    },
    {
      path: '/zhaopin',
      name: 'zhaopin',
      component: zhaopin
    },
    {
      path: '/gupiao',
      name: 'gupiao',
      component: gupiao
    }, {
      path: '/qihuo',
      name: 'qihuo',
      component: qihuo
    }, {
      path: '/jinshu',
      name: 'jinshu',
      component: jinshu
    }, {
      path: '/dazong',
      name: 'dazong',
      component: dazong
    }, {
      path: '/huobi',
      name: 'huobi',
      component: huobi
    }, {
      path: '/zhengquan',
      name: 'zhengquan',
      component: zhengquan
    }
  ]
})



