// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import {language} from './lang/language'
// 导入vuex
import store from './store/index'
import './utils/components'
import VueClipboard from 'vue-clipboard2'
Vue.use(VueClipboard)
Vue.use(ElementUI)


let type = sessionStorage.getItem('language') || '中文'
window.language = language[type]

Vue.config.productionTip = false

// 语言修改
window.setMoneyUnit = (language) => {
  let setlanguage = language || '中文'
  sessionStorage.setItem('language', setlanguage)
}
Vue.prototype.setMoneyUnit = window.setMoneyUnit
// 获取当前语言
Vue.prototype.getLanguage = () => {
  return sessionStorage.getItem('language') || '中文'
}
window.setMoneyUnit(sessionStorage.getItem('language'))
// 路由跳转地址
window.pageTo = (path) => {
  router.push({
    name: path
  })
}
Vue.prototype.pageTo = window.pageTo
// 跳转链接
Vue.prototype.toLink = (link) => {
  window.open(link)
}

window.store = store

new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



