<template>
  <!-- 顶部导航栏 -->
  <div>
    <div class="top-top-items">
      <div class="top-top" v-for="(item, key) in pageText.up" :key="key">
        <div class="top-top-item">
          <div class="top-top-item-title pointer">{{item.title}}</div>
          <div class="top-top-item-hr"></div>
        </div>
        <!-- 语言切换 -->
      </div>
      <div class="top-langItem pointer">
        <el-dropdown @command="loadPage">
          <div class="top-languageArrItem-text">{{pageText.textArr[0]}}</div>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item  v-for="(item, index) in languageArr" :key="index" :command="item.text">
              <div class="top-languageArrItem flex-center">
                <div class="top-languageArrItem-logo" :style="'background:url(' + item.logo + ')no-repeat center;background-size:100% 100%'"></div>
                <div class="top-languageArrItem-text" style="color: #333333">{{item.text}}</div>
              </div>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
  <div class="top-main flex-center">
    <div class="top-logo pointer" @click="pageTo('home')"></div>
    <div class="top-btns">
      <div class="top-left-btns">
        <div class="top-btn" v-for="(item, key) in pageText.nav" :key="key">
          <el-dropdown @command="pageTo">
            <span class="el-dropdown-link">
              <div  v-if="item.pathUrl!==''">
                <span><router-link  class="word"  :to=item.pathUrl>{{item.title}}</router-link></span>
              </div>
             <div v-else>
               <span class="word">{{item.title}}</span>
             </div>
            </span>
            <div v-if="item.arrData">
              <el-dropdown-menu style="background-color: #FFFFFF;color: #333333"  slot="dropdown">
                  <div class="nav-all" >
                    <div style="text-align: center" v-for="(val, index) in item.arrData" :key="index">
                      <div>
                        <div class="nav-title">{{val.title}}</div>
                        <div></div>
                        <el-dropdown-item style="text-align: center;color: #333333" v-for="(it, key) in val.arrData" :key="key" :command="it.pathUrl">{{it.title}}</el-dropdown-item>
                      </div>
                    </div>
                  </div>
              </el-dropdown-menu>
            </div>
            <div v-else>

            </div>
          </el-dropdown>
        </div>
      </div>
      <router-link :to=pageText.btns[0].path style="text-decoration: none">
        <div style="display: flex;justify-content: center">
          <div class="top-right-btn">{{pageText.btns[0].title}}</div>
          <div class="top-right-btn">{{pageText.btns[1].title}}</div>
        </div>
      </router-link>

    </div>
  </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      pageText: window.language.top,
      languageArr: [
        {
          logo: 'https://fuhuigala.com/static/picture/en.jpg',
          text: 'English',
        },
        {
          logo: 'https://fuhuigala.com/static/picture/ch.jpg',
          text: '中文',
        }
      ]
    }
  },
  methods: {
    loadPage (data) {
      this.setMoneyUnit(data)
      location.reload()
    },
    reslangIcon () {
      let language = this.getLanguage()
      if (language === '中文') {
        return 'https://fuhuigala.com/static/picture/ch.jpg'
      } else if (language === 'English') {
        return 'https://fuhuigala.com/static/picture/en.jpg'
      }
    }
  }
}
</script>

<style scoped>
.top-top-items
{
  display: flex;
  align-items: center;
  justify-content: flex-end;
  position:fixed;
  top:0;
  padding: 5px 17.5rem;
  min-width:93rem;
  width: 100%;
  box-sizing: border-box;
  background-color: #131721;
  z-index: 1000;
  color: #FFFFFF;
}
.top-top-item-title
{
  font-family: PingFang SC-Regular, PingFang SC;
  font-size:.875rem;
  font-weight: 500;
  line-height:1.75rem;
}
.top-top-item-hr
{

  width: 1px;
  height: 1.125rem;
  background-color: #1677FF;
}
.top-top-item
{
  width:6rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.top-top
{

  font-family: PingFang SC-Regular, PingFang SC;
  font-size:.875rem;
  font-weight: 500;
  line-height:1.75rem;
}
.nav-all
{
  display: flex;
  justify-content: space-between;
}
.top-main {
  justify-content:center;
  background: #000117;
  padding: .5rem 1.5rem;
  box-sizing: border-box;
  position: fixed;
  left: auto;
  top:3.8rem;
  z-index: 1000;
  height:5rem;
  min-width:93rem;
  width: 100%;
  margin: -1.5rem auto;
  color: #FFFFFF;
}
.top-logo {
  width: 8.875rem;
  height: 3.0625rem;
  background: url('../assets/logo.jpg') no-repeat center;
  background-size: 100% 100%;
}
.top-btns {
  display: flex;
  justify-content:space-between;
  width:75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.25rem;
}
.top-left-btns
{

  display: flex;
}
.top-btn {
  align-self: center;
  align-items: center;
  margin-left:3rem;
  cursor: pointer;

}
.word
{
  text-decoration: none;
  color:#FFFFFF;
  font-weight:400;
  font-family: PingFang SC-Regular, PingFang SC;
}
.word:hover {
  color: #1677FF;

}
.nav-title
{
  font-weight: 600;
  font-size: 1rem;
  margin-bottom: 1rem;
}
/*.top-btn + .top-btn {
  margin-left:3rem;
}*/
.top-btn-logo {
  width: 2.985rem;
  height: 3rem;
  cursor: pointer;
  position: relative;
  border-radius: 50%;
}
.top-right-btn {
  margin-left: 1rem;
  width:6.375rem;
  padding:.875rem 0;
  font-size:.875rem;
  font-weight: 500;
  color: #FFFFFF;
  text-align: center;
  line-height: 1.1875rem;
  border-radius: .25rem;
  border: .0625rem solid #1677FF;
  cursor: pointer;
  background: #1677FF;
}

.top-right-btn:hover {
  color: #FFFFFF;
  border: .0625rem solid #FFFFFF;
  background:none;
}
.el-dropdown-menu__item + .el-dropdown-menu__item {
  margin-top: 1.25rem
}
.top-langItem-logo {

  cursor: pointer;
  width: 34px;
  height: 34px;
  border-radius: 50%;
  background: url('../assets/lang.png') no-repeat center;
  background-size: 100% 100%;
}
.top-langItem
{

}
.top-languageArrItem-text
{
  font-family: PingFang SC-Regular, PingFang SC;
  font-size:.875rem;
  font-weight: 500;
  line-height: 20px;
  color: #FFFFFF;
}
</style>



