<template>
  <div>
    <div class="falv-main">
      <div class="falv-top">
        <div class="falv-top-title">{{pageText[0]}}</div>
        <div class="falv-top-text">{{pageText[1]}}</div>
      </div>
<!--      合规披露-->
      <div class="falv-medium">
        <div class="falv-medium-title">{{pageText[2]}}</div>
        <div class="falv-medium-text">{{pageText[3]}}</div>
        <div class="falv-medium-text">{{pageText[4]}}</div>
        <div class="falv-medium-text">{{pageText[5]}}</div>
        <div class="falv-medium-text">{{pageText[6]}}</div>
      </div>
      <div class="falv-medium">
        <div class="falv-medium-title">{{pageText[7]}}</div>
        <div class="falv-medium-text">{{pageText[8]}}</div>
        <div class="falv-medium-text">{{pageText[9]}}</div>
        <div class="falv-medium-text">{{pageText[10]}}</div>
      </div>
      <div class="falv-medium">
        <div class="falv-medium-title">{{pageText[11]}}</div>
        <div class="falv-medium-text">{{pageText[12]}}</div>
        <div class="falv-medium-text">{{pageText[13]}}</div>
        <div class="falv-medium-text">{{pageText[14]}}</div>
      </div>
      <div class="falv-medium">
        <div class="falv-medium-title">{{pageText[15]}}</div>
        <div class="falv-medium-text">{{pageText[16]}}</div>
        <div class="falv-medium-text">{{pageText[17]}}</div>
        <div class="falv-medium-text">{{pageText[18]}}</div>
      </div>
<!--   条款细则   -->
      <div class="falv-terms-one">
        <div v-for="(item,index) in arrData[0]" :key="index">
          <div class="falv-terms-title">{{item.title}}</div>
          <div class="falv-terms-one-items">
            <div v-for="(val,key) in item.arrData" :key="key">
              <div class="falv-terms-one-title pointer">{{val.title}}</div>
            </div>
          </div>
        </div>
      </div>
      <div class="falv-terms-two">
        <div v-for="(item,index) in arrData[1]" :key="index">
          <div class="falv-terms-title">{{item.title}}</div>
          <div class="falv-terms-two-items">
            <div v-for="(val,key) in item.arrData" :key="key">
              <div class="falv-terms-two-item">
                <div class="falv-terms-two-img" :style="'background:url('+val.img+')no-repeat center;background-size:100% 100%'"></div>
                <div>
                  <div class="falv-terms-two-title">{{val.title}}</div>
                  <div class="falv-terms-two-text">{{val.text}}</div>
                  <div class="falv-terms-two-text">{{val.text1}}</div>
                  <div class="falv-terms-two-text">{{val.text2}}</div>
                </div>
              </div>
              <div v-if="key===0">
                <div class="hr"></div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="falv-terms-three">
        <div v-for="(item,index) in arrData[2]" :key="index">
          <div class="falv-terms-title">{{item.title}}</div>
          <div class="falv-terms-text">{{item.text}}</div>
          <div class="falv-terms-three-items">
            <div v-for="(val,key) in item.arrData" :key="key">
              <div class="falv-terms-three-item">
                <div class="falv-terms-three-img" :style="'background:url('+val.img+')no-repeat center;background-size:100% 100%'"></div>
                <div>
                  <div class="falv-terms-two-title">{{val.title}}</div>
                  <div class="falv-terms-two-text">{{val.text}}</div>
                  <div class="falv-terms-two-text">{{val.text1}}</div>
                  <div class="falv-terms-two-text">{{val.text2}}</div>
                  <div class="falv-terms-two-text">{{val.text3}}</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

    </div>
    <zhunbei></zhunbei>
    <bottom></bottom>
  </div>
</template>

<script>
import Bottom from '../../components/bottom.vue'
import zhunbei from '../../components/zhunbei.vue'
export default {
  components: { zhunbei, Bottom },
  data () {
    return {
      pageText: window.language.falv.textArr,
      arrData: window.language.falv.arrData,
    }
  },
  methods: {
  }
}
</script>

<style scoped>
.hr
{
  width: 75rem;
  margin:4.5rem auto;
  height: 1px;
  background-color: #BDBDBD;
}
.falv-main
{
  width: 86.25rem;
  margin: 0 auto;
  color: #333333;
}
.falv-top
{
  padding-top: 4.5rem;
  padding-bottom: 1.5rem;
  border-bottom: 1px solid #333333;
  margin-bottom: 2rem;
}
.falv-top-title
{
  font-family: PingFang SC-Semibold, PingFang SC;
  font-size:3rem;
  font-weight: 400;
  line-height:5.5rem;
}
.falv-top-text
{
  font-family: PingFang SC-Semibold, PingFang SC;
  font-size:1rem;
  font-weight: 400;
  line-height:1.5rem;
  color: #EB5757;
}
.falv-medium
{
  margin-bottom:3rem;
}
.falv-medium-title
{
  font-family: PingFang SC-Semibold, PingFang SC;
  font-size:1.75rem;
  font-weight: 500;
  line-height:2.625rem;
  margin-bottom: 1rem;
}
.falv-medium-text
{
  font-family: PingFang SC-Semibold, PingFang SC;
  margin-bottom: 1rem;
  font-size:1rem;
  font-weight: 400;
  line-height: 1.5rem;
}
.falv-terms-title
{
  font-family: PingFang SC-Semibold, PingFang SC;
  font-size:3rem;
  font-weight: 400;
  line-height:5.5rem;
}
.falv-terms-one
{

  margin-bottom: 10rem;
}
.falv-terms-one-items
{
  width:89rem;
  margin-top: 1rem;
  display: flex;
  flex-wrap: wrap;
}
.falv-terms-one-title
{
  width:20.5rem;
  padding: 1.5rem 0;
  border-radius: .375rem;
  margin-bottom: 1.5rem;
  margin-right: 1.5rem;
  border: 1px solid #828282;
  font-family: PingFang SC-Semibold, PingFang SC;
  font-size:1.75rem;
  font-weight: 400;
  line-height:2.625rem;
  text-align: center;
}
.falv-terms-two-item
{
  display: flex;
  margin-top: 3.5rem;
  justify-content:space-between;
}
.falv-terms-two-title
{
  margin-bottom: 1rem;
  margin-top:.5rem;
  font-family: PingFang SC-Semibold, PingFang SC;
  font-size:1.75rem;
  font-weight: 500;
  line-height: 2.625rem;
}
.falv-terms-two-text
{
  width: 43.125rem;
  font-family: PingFang SC-Semibold, PingFang SC;
  font-size:1rem;
  font-weight: 400;
  line-height:1.5rem;
}
.falv-terms-two-img
{
  padding:5rem 0 5rem 0;
  width:40rem;
  height:20rem;
}
.falv-terms-text
{
  height: 8rem;
}
.falv-terms-three
{
  margin-top: 6.5rem;
  margin-bottom: 14.5625rem;
}
.falv-terms-three-img
{
  padding: 5rem 10rem;
  width: 22.5rem;
  height:8rem;
}
.falv-terms-three-item
{
  margin-top:4.5rem;
  display: flex;
  justify-content: space-between;
  padding-bottom:4.375rem ;
  border-bottom: 1px solid #BDBDBD;
}
</style>



