<template>
  <div class="chances-main">
   <div class="chances-title">{{pageText[0]}}</div>
    <div class="chances-box">
      <div v-for="(item,index) in pageText[1].icon" :key="index">
        <div class="chances-item">
          <div class="chances-item-img" :style="'background:url('+item.icon+')no-repeat center;background-size:100% 100%'"></div>
          <div class="chances-item-title">{{item.title}}</div>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      pageText:window.language.chances
    }
  }
}
</script>

<style scoped>
 .chances-main
 {
   margin: 0 auto 10rem;
   background-color: #F5F5F5;
   padding-bottom: 5rem;
   color: #333333;
 }
 .chances-title
 {
   padding-top:6.25rem;
  padding-bottom: 5rem;
   font-family: PingFang SC-Semibold, PingFang SC;
   font-size:3rem;
   font-weight: 600;
   line-height:4.1875rem;
   text-align: center;
 }
 .chances-box
 {
   display: flex;
   justify-content: space-evenly;
   width:86.25rem;
   background-color: #FFFFFF;
   margin: 0 auto;

 }
 .chances-item
 {

   padding: 5rem 0;
   text-align: center;
   width: 16.625rem;
 }
 .chances-item-title
 {
   font-family: PingFang SC-Semibold, PingFang SC;
   font-size:1.25rem;
   font-weight: 400;
   line-height:1.875rem;
   margin-top: 2.5rem;
 }
 .chances-item-img
 {
   margin: 0 auto;
   height: 5rem;
   width: 5rem;
 }
</style>



